export default {
  props: {
    active: {
      type: Number,
      default: 0
    }
  },
  methods: {
    toIndex() {
      this.$router.push(`/`)
    },
    toHealthCenter() {
      this.$router.push(`/healthCenter`)
    },
    toGiftCenter() {
      this.$router.push(`/giftCenter`)
    },
    tomemberCenter() {
      this.$router.push(`/memberCenter`)
    }
  }
}



