import request from './request'
import verifySign from './verify'
import wx from 'wx'

export function loadData(url, params) {
  const obj = verifySign(params)
  return request({
    url: url,
    method: 'post',
    data: obj
  })
}
export function getWechatSign(apiList, fn) {
  loadData(`/wechat/signature?url=${location.href}`).then((data) => {
    wx.config({
      beta: true,
      debug: false,
      appId: data.data.corpID,
      timestamp: +data.data.timestamp,
      nonceStr: data.data.nonceStr,
      signature: data.data.signature,
      jsApiList: apiList
    })
    wx.ready(fn)
  })
}



